body,
    div,
    p,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    ul,
    li,
    a,
    span,
    i,
    b {
        margin: 0;
        padding: 0
    }
    body{
        background-color: #fff;
    }
