
.searchWrap{background-color:#ddd;height:19.2rem
}
.searchWrap .header{background-color:#fff;padding:.3072rem 0
}
.searchWrap .header .search{height:.72rem;background-color:#E3E6EB;border-radius:.6rem;width:8.26rem;margin-left:.4752rem;margin-right:2.0628rem;line-height:0;position:relative
}
